@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color-background: #ddd;
  --primary: #000;
  --primaryhover: #000;
  --secondary: #990000;
  --secondaryhover: #990000;
  --cta: #FFCC00;
  --ctahover: #FFCC00;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home-img{
    background: linear-gradient( 
            3420deg
         , transparent 60%, white 90%);
    height: 501px;
    width: 100%;
    min-width: 1200px !important;
    position: absolute;
    top: 0;
}

.hover-zoom{
    transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    -webkit-transition: all 0.1s ease-in-out;
    cursor: default;
}
.home-title{
    cursor: default;
}

.hover-zoom:hover{
    -webkit-transform: scale(1.1) !important;
            transform: scale(1.1) !important;
}

.insta-social{
    width: 50px;
    height: 50px;
    padding: 10px;
}

.social-media a{
    text-decoration: none;
    color: black
}
.social-media > a:hover{
    text-decoration: none;
}

.css-selector {
    background: linear-gradient(89deg, #990000, #ffcc00);
    background-size: 400% 400%;

    -webkit-animation: coolbuttons 30s ease infinite;
    animation: coolbuttons 30s ease infinite;
}

@-webkit-keyframes coolbuttons {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes coolbuttons {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
